export default {
	data() {
		return {
			
		}
	},
	methods: {
		getUserInfo(res){
			// console.log(res)
			let {userInfo,rawData}=res.detail;
			if(userInfo){
				//弹出授权成功提示窗口
				uni.showToast({
					title:"授权成功",
					success:()=>{
						uni.reLaunch({
							url:"/pages/personal/personal?userInfo=" + JSON.stringify(userInfo)
						})
					}
				})
				uni.setStorageSync("userInfo",rawData);
				
			}else{
				//弹出授权失败提示窗口
				uni.showToast({
					title:"授权失败,请重新授权",
					icon:"none"
				})
			}
			
		}
	}
}
